@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&family=Source+Sans+Pro&display=swap);
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
}

body {
  background-color: rgb(12, 12, 12);
  font-family: 'Montserrat', sans-serif;
  color: rgb(220, 220, 220)
}

button,
input {
  font-family: 'Montserrat', sans-serif;
}

.container {
  max-width: 1140px;
  width: 95%;
  margin: 0 auto;
  padding: 1rem 0;
}

.home {
  background-image: url(https://cypresspixill.files.wordpress.com/2015/09/stars.gif);
  position: fixed;
  top: 0;
  left: 0;
  min-height: 100%;
  min-width: 100%;
  width: 100%;
  height: auto;
}

.intro {
  display: flexbox;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  text-align: center;
  padding-top: 2rem;
}

h2 {
  font-size: 2.5m;
  padding-top: 0.5rem;
}

h4 {
  font-size: 2rem;
  padding-top: 0.5rem;
}

h6 {
  font-size: 1rem;
  padding-top: 0.5rem;
  text-align: center;
}

.intro-pic {
  padding-top: 4rem;
}

.intro-pic img {
  width: 20%;
  height: auto;
  border-radius: 300px;
}

.footer {
  padding-top: 2rem;
}

a {
  padding-left: 1rem;
  color: white;
  font-size: 1rem;
}

@media (max-width: 768px) {
  .intro {
    max-width: 100%;
  }
  .intro-pic img {
    width: 50%;
  }
}

@media (max-width: 600px) {
  .intro {
    max-width: 100%;
  }
  .intro-pic img {
    width: 50%;
  }
}


.chip {
  font-size: 0.7rem;
  background: #ead4ff;
  color: #000000;
  padding: 0.3rem 0.5rem;
  border-radius: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-transform: capitalize;
}

.emptyList-wrap {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.emptyList-wrap img {
  margin-top: 1rem;
  max-width: 250px;
  width: 100%;
}

.blog-wrap {
  max-width: 700px;
  margin: 0 auto;
}

.blog-goBack {
  text-decoration: none;
  font-size: 0.8rem;
  color: #a9a9a9;
  font-weight: 500;
  margin-bottom: 2rem;
  display: block;
}
.blog-wrap header {
  text-align: center;
}

.blog-date {
  font-size: 0.8rem;
  color: #a9a9a9;
  font-weight: 500;
}

.blog-wrap img {
  width: 100%;
}

img[alt~="desc-img"] {
  padding: 1.5rem;
  width: 50%;
}

.blog-subCategory {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.blog-subCategory > div {
  margin: 1rem;
}
.blog-desc {
  padding: 1rem;
  margin-top: 1.5rem;
}

.blogItem-wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.blogItem-cover {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-radius: 20px;
  margin-bottom: 0.5rem;
}
.blogItem-wrap h3 {
  margin: 0.5rem 0 1rem 0;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.blogItem-desc {
  position: relative;
  max-height: 50px;
  overflow: hidden;
  padding-right: 0.6rem;
  font-size: 0.8rem;
  color: #a9a9a9;
}

.blogItem-desc::before {
  position: absolute;
  content: '...';
  bottom: 0;
  right: 0;
}

.blogItem-wrap footer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 1rem;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.blogItem-link {
  text-decoration: none;
  color: inherit;
}

.blogItem-author {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.blogItem-author img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 0.5rem;
}

.blogItem-author p {
  font-size: 0.6rem;
  color: #a9a9a9;
  font-weight: 600;
}

.blogList-wrap {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3rem;
}

@media (max-width: 768px) {
  .blogList-wrap {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .blogList-wrap {
    grid-template-columns: repeat(1, 1fr);
  }
}

.blogs-header {
  text-align: center;
}

.blogs-header h2 {
  font-size: 2rem;
}

.blogs-header h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.blogs-header h1 span {
  color: #a9a9a9;
}

.blogs-header p {
  color: #a9a9a9;
  font-weight: 500;
}

.searchBar-wrap {
  background-color: #cccccc;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 2.5rem auto 4rem auto;
  padding: 0.5rem;
  border-radius: 5px;
}

.searchBar-wrap form {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.searchBar-wrap input {
  background-color: #cccccc;
  outline: none;
  border: none;
}

.searchBar-wrap span {
  padding-right: 0.5rem;
  cursor: pointer;
}

.searchBar-wrap button {
  outline: none;
  border: none;
  padding: 0.3rem 1rem;
  border-radius: 5px;
  background-color: #290030;
  color: rgb(255, 255, 255);
}

.blogs-goBack {
    text-decoration: none;
    font-size: 0.8rem;
    color: #a9a9a9;
    font-weight: 500;
    margin-bottom: 2rem;
    display: block;
  }
